@import url(https://fonts.googleapis.com/css2?family=Odibee+Sans&display=swap);
body {
  font-size: 32px;
  margin: 0;
  font-family: 'Odibee Sans', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/superhero/static/media/bg.0e6bdbd9.jpg);
  background-size: cover;
  background-attachment: fixed;
  line-height: 1.3;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
a{
  text-decoration: none;
}



.charCard{
    padding: 0;
    background-size: 70px 70px, cover;
    background-repeat: no-repeat, no-repeat;
    height: 375px;
    max-width: 250px;
    margin: 10px;
    color:white;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    border-radius: 15px;
    box-shadow: 13px 10px 13px -2px rgba(0,0,0,0.75);
-webkit-box-shadow: 13px 10px 13px -2px rgba(0,0,0,0.75);
-moz-box-shadow: 13px 10px 13px -2px rgba(0,0,0,0.75);
transition: all .5s;
}

.charCard:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: 13px 10px 45px -2px rgba(0,0,0,0.75);
-webkit-box-shadow: 13px 10px 45px -2px rgba(0,0,0,0.75);
-moz-box-shadow: 13px 10px 45px -2px rgba(0,0,0,0.75);   
}
.cardName{
    padding: 2px;
    font-size: 32px;
    transition: all 0.5s;
}
.cardName::after{
    font-size: 0;
    display: -webkit-flex;
    display: flex;
    content: 'Ver detalles';
    opacity: 0;
    transition: all 0.5s;
    color:white;
}

.cardName:hover::after{
    font-size: 24px;
   opacity: 1;
}
.close{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
}
.good{
    border: #ffc107 12px solid;
}
.cardSeparator{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.788);
    padding: 5px;
}
span.good{
    border: none;
    color: #ffc107;
}
.btn{
    font-size: 24px;
}
span.bad {
    border: none;
    color: #dc3545
}
span.neutral{
    border: none;
    color: gray
}
.bad {
    border: #dc3545 12px solid;
}

.neutral {
    border: gray 12px solid
}
.stats{
    width: 200px;
}
.formik-err{
    color: #dc3545 !important;
}
.navbar-brand{
    font-size: 32px;
}
.form-control{
    font-size: 32px;
}
.accordion-item{
    background-color: rgba(2, 21, 58, 0.842);
    color: white;
    text-align: center;
    border-radius: 0 !important;
}
.accordion-button{
    font-size: 32px;
    background-color: #212529;
    color: white;
}
.accordion-button.collapsed:hover{
    color: #0c63e4;
    background-color: rgba(2, 21, 58, 0.842);
    box-shadow: inset 0 -1px 0 rgb(0 0 0 / 13%);
}
.accordion-button.collapsed::after{
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%230c63e4'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e")
}
.accordion-button:not(.collapsed) {
    color: #ffffff;
    background-color:  #212529b0;
    box-shadow: inset 0 -1px 0 rgb(0 0 0 / 13%);
}
.accordion-button:not(.collapsed):hover {
    color: #0c63e4;
    background-color: rgba(2, 21, 58, 0.842);
    box-shadow: inset 0 -1px 0 rgb(0 0 0 / 13%);
}
progress{
    vertical-align: middle;
}
.speciality{
    font-size: 50px;
    color: #ffc107;
}
.log{
    cursor: pointer;
}
.log:hover{
    color: rgb(93, 255, 255) !important;
}
.contenedorFlotante{
    position: fixed;
    top: 0px;
    z-index: 1200;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.726);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.charDetails{
    width: 60vw;
    height: 80vh;
    background-size: contain ;
    background-position: center;
    background-repeat: no-repeat;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    background-color: rgba(255, 255, 255, 0);
    border: 3px solid;
    border-image: linear-gradient(to bottom, rgba(7, 0, 0, 0), rgba(255, 255, 255, 0.541)) 1 1%;
}
.charText{
    display: -webkit-flex;
    display: flex;
    padding-left: 10px;
    font-size: 24px;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    justify-self: end;
    color: rgb(231, 223, 207);
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.856));
    width: 100%;
    height: 100%;
}

@media (max-width: 574px){
    .charDetails{
        background-size: cover;
    }
}



